@green: #009688;
@lighterGreen: #5cb85c;
@lightestGreen: #E2F4F2;
@primaryColor: @green;
@grey: #cdcdcd;
@secondaryColor: @lightestGreen;
@sideNavColor: #3c5788;


@buttonColor: @secondaryColor;
@buttonBorderColor: @secondaryColor;

@actionButtonColor: @lighterGreen;
@actionButtonBorderColor: darken(@actionButtonColor, 14%);

@resetButtonColor: @grey;
@restButtonBorderColor: darken(@resetButtonColor, 14%);

// @primaryFont: 'Open Sans Condensed', sans-serif;
@primaryBoldFont: 'Open Sans', sans-serif;

@primaryFont: 'Open Sans';
@secondardFont: 'Quattrocento+Sans';

.primaryBold {
    font-family: @primaryBoldFont;
    font-size: 2rem;
    font-weight: 600;
    text-transform: uppercase;
}